import React, { Component } from 'react';
import './App.css';

import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Card from 'react-bootstrap/Card';

import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';

import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import * as am4plugins_wordCloud from "@amcharts/amcharts4/plugins/wordCloud";
import * as am4plugins_forceDirected from "@amcharts/amcharts4/plugins/forceDirected";

am4core.useTheme(am4themes_animated);

class App extends Component {
  keyWords() {
    let chart = am4core.create("keywords", am4plugins_wordCloud.WordCloud);
    chart.fontFamily = "Courier New";
    let series = chart.series.push(new am4plugins_wordCloud.WordCloudSeries());
    series.randomness = 0.1;
    series.rotationThreshold = 0.5;
    
    series.data = window.keywordsData;
    console.log(series.data);

    series.dataFields.word = "tag";
    series.dataFields.value = "count";
    
    series.heatRules.push({
    "target": series.labels.template,
    "property": "fill",
    "min": am4core.color("#0000CC"),
    "max": am4core.color("#CC00CC"),
    "dataField": "value"
    });
    
    series.labels.template.url = "https://stackoverflow.com/questions/tagged/{word}";
    series.labels.template.urlTarget = "_blank";
    series.labels.template.tooltipText = "{word}: {value}";
    
    let hoverState = series.labels.template.states.create("hover");
    hoverState.properties.fill = am4core.color("#FF0000");
    
    this.keywordsChart = chart;
  };

  topAuthors() {
    let chart = am4core.create("topauthors", am4plugins_forceDirected.ForceDirectedTree);
    let networkSeries = chart.series.push(new am4plugins_forceDirected.ForceDirectedSeries())

    chart.data = window.topauthorsData;

    networkSeries.dataFields.value = "numCitations";
    networkSeries.dataFields.name = "author";
    networkSeries.dataFields.children = "coauthors";
    networkSeries.nodes.template.tooltipText = "{author}: {numCitations}";
    networkSeries.nodes.template.fillOpacity = 1;

    networkSeries.nodes.template.label.text = "{initials}"
    networkSeries.fontSize = 20;

    networkSeries.minRadius = 10
    networkSeries.maxRadius = 50

    networkSeries.links.template.strokeWidth = 1;
    chart.legend = new am4charts.Legend();
    chart.legend.maxHeight = 80;
    chart.legend.maxWidth = 50;
    chart.legend.scrollable = true;
    chart.legend.position = "down";

    let hoverState = networkSeries.links.template.states.create("hover");
    hoverState.properties.strokeWidth = 3;
    hoverState.properties.strokeOpacity = 1;

    networkSeries.nodes.template.events.on("over", function(event) {
        event.target.dataItem.childLinks.each(function(link) {
        link.isHover = true;
        })
        if (event.target.dataItem.parentLink) {
        event.target.dataItem.parentLink.isHover = true;
        }

    })

    networkSeries.nodes.template.events.on("out", function(event) {
        event.target.dataItem.childLinks.each(function(link) {
        link.isHover = false;
        })
        if (event.target.dataItem.parentLink) {
        event.target.dataItem.parentLink.isHover = false;
        }
    })
    this.topauthorsChart = chart;
  };
  
  topJournals() {
    let chart = am4core.create("topjournals", am4charts.PieChart);
    chart.hiddenState.properties.opacity = 0; // this creates initial fade-in
    
    chart.data = window.topjournalsData;
    console.log(chart.data);

    chart.radius = am4core.percent(80);
    chart.innerRadius = am4core.percent(40);
    chart.startAngle = 180;
    chart.endAngle = 360;  

    let series = chart.series.push(new am4charts.PieSeries());
    series.dataFields.value = "numPapers";
    series.dataFields.category = "journalName";

    series.slices.template.cornerRadius = 10;
    series.slices.template.innerCornerRadius = 7;
    series.slices.template.draggable = true;
    series.slices.template.inert = true;
    series.alignLabels = false;

    series.ticks.template.disabled = true;
    series.labels.template.text = "{value.percent.formatNumber('#.0')}%";
    series.labels.template.radius = am4core.percent(-40);
    series.labels.template.fill = am4core.color("white");
    series.labels.template.relativeRotation = 90;

    series.hiddenState.properties.startAngle = 90;
    series.hiddenState.properties.endAngle = 90;

    chart.legend = new am4charts.Legend();
    chart.legend.maxHeight = 120;
    chart.legend.scrollable = true;
    chart.legend.position = "bottom";
  };

  componentDidMount() {
    this.keyWords();
    this.topAuthors();
    this.topJournals();
  }


  componentWillUnmount() {
    if (this.keywordsChart) {
      this.keywordsChart.dispose();
    }
    if (this.topauthorsChart) {
      this.topauthorsChart.dispose();
    }
    if (this.topjournalsChart) {
      this.topjournalsChart.dispose();
    }
  };

  render() {
    return (
      <>
        <Navbar bg="dark" variant="dark">
          <Navbar.Brand href="#home">Scholarview</Navbar.Brand>
          <Nav className="mr-auto">
            <Nav.Link href="#home">Home</Nav.Link>
          </Nav>
          <Form inline>
            <FormControl type="text" placeholder="Search" className="mr-sm-2" />
            <Button variant="outline-info">Search</Button>
          </Form>
        </Navbar>

        <Carousel>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="/static/user/UIUC.jpg"
              alt="First slide"
            />
            <Carousel.Caption>
              <h3>First slide label</h3>
              <p>Nulla vitae elit libero, a pharetra augue mollis interdum.</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="/static/user/UIUC.jpg"
              alt="Second slide"
            />
            <Carousel.Caption>
              <h3>Second slide label</h3>
              <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img
              className="d-block w-100"
              src="/static/user/UIUC.jpg"
              alt="Third slide"
            />

            <Carousel.Caption>
              <h3>Third slide label</h3>
              <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>

      <Container fluid><Row>
        <Col>
          <Card style={{border: "None"}}>
            <Card.Body>
              <Card.Title>Key Words</Card.Title>
              <Card.Text>
                
              </Card.Text>
            </Card.Body>
            <div id="keywords" style={{ width: "100%", height: "400px" }}></div>
          </Card>
        </Col>

        <Col>
          <Card style={{border: "None"}}>
            <Card.Body>
              <Card.Title>Favorite Journals</Card.Title>
              <Card.Text>
                Some quick example text to build on the card title and make up the bulk of the card's content.
              </Card.Text>
            </Card.Body>
            <div id="topjournals" style={{ width: "100%", height: "400px" }}></div>
          </Card>
        </Col>

        <Col>
          <Card style={{border: "None"}}>
            <Card.Body>
              <Card.Title>Coauthors</Card.Title>
              <Card.Text>
                Some quick example text to build on the card title and make up the bulk of the card's content.
              </Card.Text>
            </Card.Body>
            <div id="topauthors" style={{ width: "100%", height: "400px" }}></div>
          </Card>
        </Col>


      </Row></Container>
      </>
    );
  }
}

export default App;